// Class Prefix
$pluginPrefix: "coca-image-compare" !default;


/* You can remove this page div in your website */
.#{$pluginPrefix}-triple-wrapper {
	--coca-image-compare-handle-color: #fff;
	--coca-image-compare-handle-stroke: 3px;
	--coca-image-compare-handle-circle-width: 40px;
	--coca-image-compare-handle-circle-height: 40px;
	--coca-image-compare-handle-box-shadow: 3.5px 0 7px rgba(100, 100, 100, 0.2);
	--coca-image-compare-handle-triangle-color: var(--coca-image-compare-handle-color);
	--coca-image-compare-handle-triangle-size: 6px;
	--coca-image-compare-handle-triangle-position: 5px;
	--coca-image-compare-handle-radius: 1000px;
	--coca-image-compare-overlay-bg: rgba(0, 0, 0, 0.5);
	--coca-image-compare-overlay-label-color: #fff;
	--coca-image-compare-overlay-label-bg: rgba(255, 255, 255, .2);
	--coca-image-compare-overlay-label-height: 38px;
	--coca-image-compare-overlay-label-width: 90px;
	--coca-image-compare-overlay-label-padding: 20px;
	--coca-image-compare-overlay-label-font-size: 13px;
	--coca-image-compare-overlay-label-letter-spacing: 0.1em;
	--coca-image-compare-label-radius: 2px;

	display: flex;
	//justify-content: center;

	width: 100%;
	height: 100%;
	overflow: hidden;

	* {
		margin: 0;

		&:not(.label) {
			box-sizing: border-box;
			-webkit-touch-callout: none;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
		}
	}

	/* Our coca-image-compare-container */
	.#{$pluginPrefix}-triple-container {
		width: 100%;
		height: auto;
		max-height: 100vh;
		overflow: hidden;
		transform: translate3d(0%, 0%, 0);
		animation: fadeIn 800ms;

		@keyframes fadeIn {

			0% {
				opacity: 0;
			}

			100% {
				opacity: 1;
			}
		}

		&:hover {
			//.overlay,
			.label {
				opacity: 1;
			}

			.overlay {
				background-color: var(--coca-image-compare-overlay-bg);
			}

			&:has(.scrolling) .overlay {
				background-color: transparent;
			}
		}
	}

	/* Our image information */
	.bottom,
	.middle,
	.top {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		pointer-events: none;
		background-color: #fff;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;

		img {
			max-width: none;
			height: auto;
			object-fit: contain;
		}

		.label {
			position: absolute;
			width: var(--coca-image-compare-overlay-label-width);
			min-width: max-content;
			padding: 0 var(--coca-image-compare-overlay-label-padding);
			font-size: var(--coca-image-compare-overlay-label-font-size);
			line-height: var(--coca-image-compare-overlay-label-height);
			color: var(--coca-image-compare-handle-color);
			text-align: center;
			background: var(--coca-image-compare-overlay-label-bg);
			border-radius: var(--coca-image-compare-label-radius);
			opacity: 0;
			//display: none;
			transition: 300ms ease-in-out;
		}
	}

	.bottom {
		//background: #E57C23;

		.label {
			right: 10px;
			bottom: 10px;
		}
	}

	.middle {
		//background: #E8AA42;

		.label {
			bottom: 10px;
			left: 75%;
		}
	}

	.top {
		//background: #F8F1F1;
		width: 125px;

		.label {
			bottom: 10px;
			left: 10px;
		}
	}

	&.handle-bottom {
		padding-bottom: 40px;

		.#{$pluginPrefix}-triple-container {
			overflow: visible;
		}
	}

	.triple-handle-wrapper {
		position: absolute;
		bottom: -22px;
		width: 100%;
		height: 10px;
		margin-right: 0;
		background: rgba(0, 0, 0, .25);
		border-radius: 30px;

		.scroller {
			width: unset;
			height: unset;
			background: #fff;
			box-shadow: none;

			&.scroller-top,
			&.scroller-middle {
				--coca-image-compare-handle-circle-height: 19px;
				top: 0;
				padding: 0;
				margin-top: 0;
				border: 0;
				transform: none;

				&:after {
					margin-left: 0;
				}

				&:before {
					display: none;
				}
			}

			.scroller__thumb {
				height: 10px;
				//width: 30px;
				border: none !important;
				border-radius: 5px;

				.left-arrow,
				.right-arrow {
					display: none;
				}
			}
		}
	}

	.scroller {
		position: absolute;
		top: 50%;
		left: 100px;
		z-index: 10;
		margin-left: calc(-1 * var(--coca-image-compare-handle-stroke) + 3px);
		pointer-events: auto;
		cursor: pointer;
		border: var(--coca-image-compare-handle-stroke) solid var(--coca-image-compare-handle-color);
		border-radius: var(--coca-image-compare-handle-radius);
		box-shadow: var(--coca-image-compare-handle-box-shadow);
		//background-color: var(--coca-image-compare-handle-color);
		opacity: 0.9;
		transition: 300ms ease-in-out;
		transition-property: opacity, background-color, border-color !important;
		transform: translateY(-50%);

		&:hover {
			opacity: 1;
		}

		&:before,
		&:after {
			position: absolute;
			left: 50%;
			z-index: 30;
			display: block;
			width: 3px;
			height: 9999px;
			margin-left: -1px;
			content: " ";
			box-shadow: 3.5px 0 7px rgba(100, 100, 100, 0.2);
			transition: 100ms ease-in-out;
		}

		&:before {
			top: calc(var(--coca-image-compare-handle-circle-height) + var(--coca-image-compare-handle-stroke));
		}

		&:after {
			bottom: calc(var(--coca-image-compare-handle-circle-height) + var(--coca-image-compare-handle-stroke));
		}

		&.scrolling {
			pointer-events: none;
			opacity: 1;
			// z-index: 1;
		}

		&.scroller-middle {
			top: calc(50% + calc(var(--coca-image-compare-handle-circle-height) / 2 + 25px));

			&:before,
			&:after {
				background: var(--coca-image-compare-handle-color);
			}
		}

		&.scroller-top {
			top: calc(50% - calc(var(--coca-image-compare-handle-circle-height) / 2 + 25px));

			&:before,
			&:after {
				background: var(--coca-image-compare-handle-color);
			}
		}

		.scroller__thumb {
			position: relative;
			width: var(--coca-image-compare-handle-circle-width);
			height: var(--coca-image-compare-handle-circle-height);

			.left-arrow,
			.right-arrow {
				position: absolute;
				top: calc(50% - 6px);
				border: 6px inset transparent;
			}

			.left-arrow {
				left: 3px;
				border-right-color: #fff;
			}

			.right-arrow {
				right: 3px;
				border-left: 6px solid #fff;
			}
		}
	}

	// style for sculler.
	//.image-wrapper .label, .scroller {
	//  z-index: 10;
	//}

	.overlay {
		//opacity: 0;
		position: absolute;
		width: 100%;
		height: 100%;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		background-color: transparent;
		transition: 300ms ease-in-out;
	}
}
